.nav {
  padding-top: 20px;
  display: flex;
  gap: 57px;
  @media screen and (max-width: 475px) {
    gap: 30px;
  }

  &__link {
    color: black;
    font-size: 24px;
    font-weight: 500;

    &:hover {
      text-decoration-line: underline;
    }
  }

  .nav__link.active {
    text-decoration: underline;
  }
}
