.error {
  &__container {
    display: flex;
    flex-direction: column;
    align-items: center;

    &__number {
      font-size: 155px;
      font-weight: 700;
      color: $primary-color;
      height: 150px;
    }

    &__text {
      color: $primary-color;
      padding-bottom: 85px;
    }

    &__link {
      color: black;
      padding-bottom: 85px;
      &:hover {
        text-decoration: underline;
      }
    }
  }
}

@media (max-width: $breakpoint-tablette) {
  .error {
    &__container {
      display: flex;
      flex-direction: column;
      align-items: center;
      padding-top: 70px;
      padding-bottom: 70px;

      &__number {
        font-size: 96px;
      }
    }
  }
}
